const MainModal = (props) => {

    const [importData, setImportData] = React.useState({
        file: '',
    });

    const [submitting, setSubmitting] = React.useState(false);

    const [errors, setErrors] = React.useState({})

    React.useEffect(() => {


    }, []);


    const _fileChange = async (e) => {
        const file = e.target.files[0];
        setErrors({ ...errors, file: '' })
        console.log(file)
        getBase64(file, (result) => {
            console.log(result);
            console.log(result.substring(result.indexOf(":") + 1, result.indexOf(";")))
            if (result.substring(result.indexOf(":") + 1, result.indexOf(";")) !== 'text/csv') {
                setErrors({ ...errors, file: 'Provide CSV file.' });
            } else {
                setErrors({ ...errors, file: '' });

            }

            let encoded = result.toString().replace(/^data:(.*,)?/, '');
            if ((encoded.length % 4) > 0) {
                encoded += '='.repeat(4 - (encoded.length % 4));
            }
            console.log(encoded)
            setImportData({ ...importData, file: encoded })
            // setUserProfile({ ...userProfile, profileImage: result, profileImageName: result, })
        })
    }



    const _submit = () => {


        const requestJson = {
            'action': 'import',
            'csv_file': importData.file
        }

        if (errors.file) return;

        console.log(requestJson)
        setSubmitting(true);
        importItems(requestJson)
            .then(res => {
                console.log(res);
                setSubmitting(false)
                if (res.json.status) {
                    props.onHide();
                    props.history.replace(props.match.url);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => {
                console.log(err);
                setSubmitting(false)
            })
    }
    const _cancel = async () => {
        await setSubmitting(false)
        props.onHide();
    }



    return (
        <>
            <Modal.Header style={{ margin: 'auto 5px', display: 'flex', justifyContent: 'space-between' }}>
                <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Import Items</Modal.Title>

                {/* <button
                    className="btn btn-primary btn-sm  font-weight-bold"
                    onClick={() => { }}
                > Download Sample File</button> */}
            </Modal.Header>

            <Modal.Body
                style={{ paddingLeft: '30px' }}
            >

                <Row>
                    <Col md={12}>
                        <Form.Group className='mb-2' >
                            <Form.Label>CSV file</Form.Label>
                            <Form.File
                                label={importData.file ? 'File Selected' : "No file selected"}
                                data-browse="Browse"
                                custom
                                onChange={_fileChange}
                                accept=".csv"
                            />
                            <div style={{ padding: '5px' }}></div>
                            <ErrorBody>{errors.file}</ErrorBody>
                        </Form.Group>
                    </Col>
                </Row>

                <div className="d-flex justify-content-end" >
                    <Button
                        variant="warning"
                        onClick={_cancel}
                    >
                        Cancel
                                </Button>
                    {importData.file ?
                        <Button
                            disabled={submitting}
                            variant="primary"
                            onClick={_submit}
                        >
                            {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Processing...</> : 'Submit'}

                        </Button> :
                        null
                    }
                </div>
            </Modal.Body>
        </>
    );
}

const ImportItems = ({ staticContext, ...rest }) => {
    return <Modal
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
        {...rest}

    >
        <MainModal
            show={rest.show}
            onHide={rest.onHide}
            {...rest}
        />
    </Modal>

}

export default ImportItems;
