import React, { Suspense } from 'react';
import { Row, Col, Card, Table, Image } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import { getItems, deleteItems } from '../../api/inventory/itemApi';
import Loader from '../../App/components/Reusables/Loader';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import { getItemsSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';
import DEMO from '../../globals/constant';
import ItemsFilter from '../../App/components/SearchFilters/ItemsFilter';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import ExportItems from './ExportItems';
import ImportItems from './ImportItems';

const Items = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [itemsArr, setItemsArr] = React.useState([]);
    const [deleteData, setDeleteData] = React.useState({ showModal: false, itemId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)
    const [exportModalShowing, setExportModalShowing] = React.useState(false)
    const [importModalShowing, setImportModalShowing] = React.useState(false)



    React.useEffect(() => {
        let controller = new AbortController();

        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getItemsSearchFilterObject(props),
            ...getStartEnd(props)
        }

        if (props.itemCategoryId) {
            requestJson.filter = 'category';
            requestJson.category_id = props.itemCategoryId;
        }

        getItems(requestJson, controller.signal)
            .then((res) => {
                console.log(res, 'from item table');
                if (res.json.status) {
                    setItemsArr(res.json.items);
                    setInstanceCount(res.json.count)
                    setLoading(false)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
        return () => {
            controller.abort()
        }
    }, []);




    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, itemId: id });
    }

    const _export = () => {
        setExportModalShowing(!exportModalShowing);
    }

    const _import = () => {
        setImportModalShowing(!importModalShowing);
    }

    const _delete = (toBeDeletedId) => {
        deleteItems({ action: 'delete', items_id: [toBeDeletedId] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedItem = itemsArr.find(item => item.id === toBeDeletedId);
                    let tempArr = itemsArr;
                    tempArr.splice(tempArr.indexOf(deletedItem), 1);
                    await setItemsArr([...tempArr])
                    setDeleteData({ showModal: false, itemId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Item deleted successfully!', background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }

    const ItemsList = itemsArr.map((item, index) => {
        var thumbnailImage = item.images.find(img => img.category === 'THUMBNAIL');
        if (!thumbnailImage) {
            thumbnailImage = item.images[0];
        }
        return (
            <tr key={index}>
                <th scope="row">{item.id}</th>
                <td className="imageTd">
                    {thumbnailImage ?
                        <Image
                            src={thumbnailImage ? `${DEMO.SERVER_URL}${thumbnailImage.image}` : "#"}
                            thumbnail={true}
                            style={{ height: '46px', minWidth: '46px', width: '46px', objectFit: 'cover' }}
                            alt="..."
                        /> : null}
                </td>
                <td
                    onClick={() => {
                        props.history.push('/item/' + item.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{item.name}</td>
                <td>{item.barcode}</td>
                <td>{item.category_str}</td>
                <td>{item.average_cost_price}</td>
                <td>{item.sales_price}</td>
                <td>{item.stock}</td>
                <td>{item.sold}</td>
                <td>{item.weight} {""} {item.weight_unit}</td>
                <td>{item.vat_enabled ? 'Yes' : 'No'}</td>
                {/* <td>{item.is_active ? 'Yes' : 'No'}</td> */}
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/item/update/' + item.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(item.id)}
                    />
                </td>
            </tr>
        )
    })
    return (
        <Aux>

            <Suspense fallback={<Loader />}>
                <Row className="d-flex justify-content-between pageHead" >
                    <h4 className="m-b-10" style={{ lineHeight: '33px' }}>Items</h4>
                    <div>

                        <button
                            className="btn btn-primary btn-sm shadow-2 mb-4 font-weight-bold"
                            onClick={() => _import()}
                        > <i className="fa fa-upload text-c-white f-15 m-r-5" />Import</button>
                        <button
                            className="btn btn-primary btn-sm shadow-2 mb-4 font-weight-bold"
                            onClick={() => _export()}
                        > <i className="fa fa-download text-c-white f-15 m-r-5" />Export</button>
                        <button
                            className="btn btn-primary btn-sm shadow-2 mb-4 font-weight-bold"
                            onClick={() => props.history.push(`item/create`)}
                        > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                    </div>
                </Row>
                <Row>
                    <Col>

                        <Card>
                            <Card.Body>
                                <ItemsFilter />
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>#</th>
                                            <th>Image</th>
                                            <th>Name</th>
                                            <th>Barcode</th>
                                            <th>Category</th>
                                            <th>Cost Price</th>
                                            <th>Sales Price</th>
                                            <th>Stock</th>
                                            <th>Sold</th>
                                            <th>Weight</th>
                                            <th>VATable</th>
                                            {/* <th>Enabled</th> */}
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {ItemsList}
                                    </tbody>
                                </Table>
                                {isEmpty(itemsArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /><br /></> : ''}

                                {loading ? <Spinner /> : null}
                                <Paging instanceCount={instanceCount} />
                            </Card.Body>
                        </Card >
                        <DeleteModal
                            show={deleteData.showModal}
                            onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                            onDelete={() => _delete(deleteData.itemId)}
                        />
                    </Col>
                </Row>

                <ExportItems
                    show={exportModalShowing}
                    onHide={() => setExportModalShowing(false)}
                    {...props}
                />

                <ImportItems
                    show={importModalShowing}
                    onHide={() => setImportModalShowing(false)}
                    {...props}
                />
            </Suspense>
        </Aux>
    );
}

export default Items;