import React from 'react';
import { Row, Col, Form, Button, Modal } from 'react-bootstrap';
import ReactSelect from 'react-select';
import makeAnimated from 'react-select/animated';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { exportItems, getItemFields } from '../../api/inventory/itemApi';

const animatedComponents = makeAnimated();



const MainModal = (props) => {

    const [fieldsArr, setFieldsArr] = React.useState([]);
    const [selectedFields, setSelectedFields] = React.useState([])


    React.useEffect(() => {

        const controller = new AbortController()

        getItemFields({ "action": "get" }, controller.signal)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    const rcds = res.json.fields.map((instance, index) => ({ label: instance, value: instance }))
                    setFieldsArr(rcds)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });

        return () => {
            controller.abort()
        }
    }, []);

    const _fieldsArrChange = (selectedFields) => {
        selectedFields = selectedFields || []
        setSelectedFields([...selectedFields])
    }
    const queryString = props.location.search;


    const _export = (type) => {

        const queryString = props.location.search;
        const queryParams = new URLSearchParams(queryString) || null;
        let itemName = queryParams.get('itemName') || null;
        let barcode = queryParams.get('barcode') || null;
        let category = queryParams.get('category') || null;
        // let categoryName = queryParams.get('categoryName') || null;
        let weightFrom = queryParams.get('weightFrom') || null;
        let weightUpto = queryParams.get('weightUpto') || null;
        let cpFrom = queryParams.get('cpFrom') || null;
        let cpUpto = queryParams.get('cpUpto') || null;
        let stockFrom = queryParams.get('stockFrom') || null;
        let stockUpto = queryParams.get('stockUpto') || null;
        let spFrom = queryParams.get('spFrom') || null;
        let spUpto = queryParams.get('spUpto') || null;
        let soldFrom = queryParams.get('soldFrom') || null;
        let soldUpto = queryParams.get('soldUpto') || null;

        const requestJson = {
            "action": "export",
            "export": type,
            "filters": {
                'name': itemName,
                'barcode': barcode,
                'weight': !(weightFrom || weightUpto) ? null :
                    {
                        'from': weightFrom,
                        'upto': weightUpto
                    },
                'average_cost_price': !(cpFrom || cpUpto) ? null :
                    {
                        'from': cpFrom,
                        'upto': cpUpto
                    },
                'stock': !(stockFrom || stockUpto) ? null :
                    {
                        'from': stockFrom,
                        'upto': stockUpto
                    },
                'sales_price': !(spFrom || spUpto) ? null :
                    {
                        'from': spFrom,
                        'upto': spUpto
                    },
                'category': category,
                'sold': !(soldFrom || soldUpto) ? null :
                    {
                        'from': soldFrom,
                        'upto': soldUpto
                    }
            },
            'start': 0,
            'end': 9999999,
            'selected_fields': selectedFields.map(instance => instance.value)
        }

        console.log(JSON.stringify(requestJson));
        exportItems(requestJson)
            .then(res => {
                console.log(res);
                if (res.json.status) {
                    props.onHide();
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => console.log(err))

    }



    return (
        <>
            <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Export Items</Modal.Title>
            </Modal.Header>

            <Modal.Body
                style={{ paddingLeft: '30px' }}
            >

                <Row>
                    <Col md={12}>
                        <Form.Group  >
                            <Form.Label>Columns to be included</Form.Label>
                            <ReactSelect
                                closeMenuOnSelect={false}
                                components={animatedComponents}
                                value={selectedFields}
                                // defaultValue={[colourOptions[4], colourOptions[5]]}
                                onChange={(selectedFields) => _fieldsArrChange(selectedFields)}
                                isMulti
                                options={fieldsArr}
                                placeholderValue="here"
                            />

                        </Form.Group>
                    </Col>
                </Row>

                <div className="d-flex justify-content-end" >
                    <Button
                        variant="primary"
                        onClick={() => _export('pdf')}
                    >
                        Export PDF
                    </Button>

                    <Button
                        variant="primary"
                        onClick={() => _export('excel')}
                    >
                        Export XlSX
                    </Button>
                </div>
            </Modal.Body>
        </>
    );
}

const ExportItems = ({ staticContext, ...rest }) => {
    return <Modal
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
        {...rest}

    >
        <MainModal
            show={rest.show}
            onHide={rest.onHide}
            {...rest}
        />
    </Modal>

}

export default ExportItems;
